<script>
import VueFaqAccordion from "../VueFaqAccordion";
import { VueAgile } from "vue-agile";
import { baseUrl } from '@/utils/helpers'

export default {
  mounted() {
    window.scrollTo(0, 0);
    this.$store.dispatch('setTitle', '   ');
  },
  components: {
    VueFaqAccordion,
    VueAgile
  },
  data() {
    return {
      contact: { name: "", lastname: "", patronym: "", email: "", message: "" },
      options2: {
        autoplay: true,
        dots: false,
        navButtons: false,
        slidesToShow: 1,
        responsive: [
          {
            breakpoint: 1000,
            settings: {
              navButtons: false,
              dots: true,
              slidesToShow: 4
            }
          }
        ]
      },
      authors: [
        {
          name: " ",
          description: "",
          image: "https://i.imgur.com/Oj90YWZ.png"
        },
        {
          name: " ",
          description: "",
          image: "https://i.imgur.com/AMkZcXP.png"
        },
        {
          name: " ",
          description: "",
          image: "https://i.imgur.com/QiQYCTj.png"
        },
        {
          name: " ",
          description: "",
          image: "https://i.imgur.com/6IaGSJW.png"
        },
        {
          name: " ",
          description: "",
          image: "https://i.imgur.com/PkVWpoi.png"
        },
        {
          name: " ",
          description: "",
          image: "https://i.imgur.com/pSyj7Ah.png"
        },
        {
          name: " ",
          description: "",
          image: "https://i.imgur.com/WBCYaTe.png"
        },
        {
          name: " ",
          description: "",
          image: "https://i.imgur.com/az0XK5Z.png"
        },
        {
          name: " ",
          description: "",
          image: "https://i.imgur.com/BFabOJy.png"
        },
        {
          name: " ",
          description: "",
          image: "https://i.imgur.com/XXNdyQU.png"
        }
      ]
    };
  },
  methods: {
    postContact() {
      if (
        !this.contact.name ||
        !this.contact.lastname ||
        !this.contact.patronym ||
        !this.contact.email ||
        !this.contact.message
      ) {
        this.$toast.warning("  !");
        return;
      }
      if (
        this.contact.name > 100 ||
        this.contact.lastname > 100 ||
        this.contact.patronym > 100 ||
        this.contact.email > 100 ||
        this.contact.message > 800
      ) {
        this.$toast.warning("   !");
        return;
      }
      let postParams = {
        name: this.contact.name,
        lastname: this.contact.lastname,
        patronym: this.contact.patronym,
        email: this.contact.email,
        message: this.contact.message
      };
      this.axios
        .post(baseUrl + "/api/v1/contact", postParams)
        .then(
          (this.contact.name = ""),
          (this.contact.lastname = ""),
          (this.contact.patronym = ""),
          (this.contact.email = ""),
          (this.contact.message = ""),
          this.$toast.success("  .  !")
        );
    }
  }
};
</script>

